#timer-label{
    position: absolute;
    top: 10px;
    text-align: center;
    left: 50%;
    transform: translate(-50%);
    font-size: 30px;
    font-weight: bold;
   
}
.timeViewer{
    width:  200px;
    height: 50px;
    background-color: rgb(2, 31, 58);
    position: absolute;
    top: 97px;
    left: 90px;
    border: solid white;
    border-color: #aaa;
   
    
    

}

.timerZone{
    width: 380px;
    height: 380px;
    border: solid black;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 350px;
    border-color: #eee;
    border-radius: 100%;
    background-color: black;
    font-family: "Digital";
   z-index: -10;
   
}
